      <div class="race text-container">
        <h2>Gender</h2>
        <p class="text-info">
          Gender discrimination describes the situation in which people are
          treated differently simply because they are male or female, rather
          than on the basis of their individual skills or capabilities. Gender
          discrimination is one of the most widespread types of discrimination.
          Girls and women suffer most of the negative impact of rigid gender
          norms and roles - they are more likely to experience restrictions of
          their freedom and mobility, experience immense levels of violence and
          harassment across the globe, and have fewer opportunities to choose
          how to live their lives. Over the years, the world has gotten closer
          to achieving gender equality. There is a better representation of
          women in politics, more economic opportunities, and better healthcare
          in many places of the world. However, in a range of counties, gender
          discrimination still is an up-to-date problem and needs to be changed,
          which can only be done by reaching an equal level in surrounding
          countries. By watching and comparing these movies in these specific
          countries, we can raise our awareness of these bad habits and
          gradually move in the right direction. Also, to reach this level, both
          men and women should be united, having in mind the same idea. In the
          comparison of the gender discrimination levels between the US, Great
          Britain, India, and South Korea, we can notice that there are both
          similarities and differences regarding the different regions. This can
          be explained by the historical differences and backgrounds of each of
          those cultures. The movies cannot clearly demonstrate the history
          and the presence of gender discrimination in these specific areas as
          not everything can be depicted and illustrated in the film, but
          instead, movies can concentrate on a particular aspect of
          discrimination that is the most visible in the timespan when the
          events were taken place. At the same time, however, movies can convey
          the general concept by showing the essence of it in a specific moment
          of a time.For example, in the movie “Suffragette,” women are
          fighting for their rights to vote, and they hope that with that their
          role in society will be more, and they will reach a level where they
          would not be subjected to work only in the laundry or other such
          places as the majority of them are working and are treated like the
          ones inferior to men. In movies “On the basis of sex” and “Kim Ji
          Young: Born in 1982,” the women are trying to change something to be
          treated as an equal with men and simultaneously making the strong
          fundament for changing mindsets of people about gender discrimination.
          And if there were not the factor of their husbands who were always
          there to help them and support, they would not be able to change
          anything and reach so far. So, we may assume from here that to change
          the gender discrimination, the actions of females are not sufficient
          enough and to reach a radical change, both men and women should be
          actively involved in the process of fighting discrimination. As not
          everyone was thinking and nurtured in the same manner and culture, the
          ones who realize the importance of this issue and the wrongness of
          sorting people out of their gender should try to raise the awareness
          of the society, and without it, the changes cannot be radical.
          There are also differences in the way gender discrimination is
          expressed in some different regions as we believe that it comes from a
          diverse background of a people and the historical factor of those
          specific countries. If we relate the movie “Bend it like Beckham” with
