        <h2>Religion</h2>
        <p class="text-info">
          Religious discrimination is treating individuals differently because
          of their religious beliefs and practices and/or their request for
          accommodations of their religious beliefs and practices. It also
          includes treating individuals differently because of their lack of
          religious beliefs or practices. Religious discrimination is quite
          controversial. Based on the movies we saw; we have derived that
          religious discrimination can come in many forms. It can range from
          people being discriminated against based on what religions they belong
          to and their spiritual habits, or whether they have completely diverse
          belief systems, such as choosing nature over God's existence or having
          agnostic views. Furthermore, religious discrimination does not
          recognize scales: it does not always have to extend either national or
          international levels. People can face religious discrimination even
          within the small scale of their families. In the movies, religious
          discrimination had both differences and similarities based on the
          chosen countries. The differences and the similarities, we suppose,
          are based on the history of that specific country, its culture, and
          traditions. The movie we have chosen to represent USA's religious
          discrimination is "The God is Dead". The movie had two religious
          aspects: a university student was being discriminated against for his
          Christian views by an atheist philosophy professor, and a girl who got
          kicked out of her home because of her religious beliefs. In the first
          case, the philosophy instructor on the first day of classes made his
          students sign papers that the topic of God's existence would be
          avoided during the semester, promising that the students would be able
          to bypass the section of the class that they got the lowest grade for.
          If they refuse to write the sentence "God Is Dead" and put their
          signature under it, they will lose thirty percent of their final
          grade. Out of sixty people present, only one Christian student
          opposed. As a matter of fact, those fifty-nine students did not even
          question the professor's requirement; they either were religiously
          unaffiliated or atheists as well. This we can directly link to the US
          statistics and history of religion, claiming that atheists make up
          approximately one-fifth of the US population. We do believe that the
          reason of the above-mentioned statistics and discrimination type in
          the film is largely based upon the ethnic diversity of the US
          population. Moreover, taking into account the forward-looking
          viewpoints of the US people, the most probability of experiencing this
          type of religious discrimination are the US communities. This is why
          religious discrimination of this type is not seen in other countries
          that we examined. The second case of religious discrimination in the
          movie was about a girl that came from a traditional Muslim immigrant
          family but failed to be a "deserving" daughter for her father. She did
          not take the religion that her father passed to her as an innate part
          of her identity, rather challenged her mindset to understand what
          religion she belongs to, i.e. Christianity. Because of this she got
          kicked out of her own house by her father, who chose his religious
          identity over being a father. We have also encountered this type of
          discrimination when examining Korea and the movie "Minari". In that
          case, we also saw the family as an indirect educational institution
          and directly influences a child's religious orientation. The two kids
          were constantly told to pray whenever necessary and whenever not, and
          if they didn't pray hard enough and frequently enough, they would be
          punished by their mother. This discrimination and also the
          discrimination towards the above-mentioned Muslim girl are examples of
          within-family discrimination. Of course, the moral upbringing that
          people from their families has an impact on what an individual grows
          up into. But in these two cases, the discrimination is apparent.
          Parents forcefully make their offspring profess their religion and do
          not accept otherwise. The more large-scale discrimination is portrayed
          in the other two films: in the case of India and Britain. In both
          cases, religious discrimination is towards a specific group of people.
          In the case of India, Muslims are a religious minority compared to
          Hindus, and the film "My Name is Khan" depicted the discrimination
          that Indian Muslims face because of it. Similarly, in the case of
          Britain, we see a general pattern of discriminating against Muslim
          minorities based on the "Blinded by the light" movie. These
          discriminations are large-scale and come from the background of the
          countries. In the case of Korea, we see a difference: because Korea
          has comparatively less ethnic diversity, their religious
          discrimination is primarily family-based. In contrast, in the two
          countries mentioned above, Muslim minorities' discrimination based on
          the movies is against the whole group of people. To sum up, we would
          like to mention that drawing parallels between these countries 
